.product__container {
    background-color: white;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
}

.product__title {
    font-size: 0.9rem;
    color: grey;
}

.product__price {
    font-weight: bolder;
    font-size: 1rem;
}

.product__image {
    max-height: 13rem;
    object-fit: contain;
    margin-bottom: .75rem;
    margin-top: auto;
}

.product__ratingContainer {}

.product__starIcon {
    color: #FFCA44;
}

.product__button {
    border-radius: .1rem;
    font-size: .7em;
    font-weight: 800;
    width: 10em;
    background-color: #FFCA44;
    cursor: pointer;
    background-color: #FFCA44;
    border: 1px solid #665019;
    padding: .3em;
}