.home__container {}

.home__image {
    width: 100%;
    object-fit: contain;
    z-index: -1;
    mask-image: linear-gradient(to bottom, rgb(245, 66, 66, 1), rgba(131, 131, 131, 0));
    margin: -120px 0;
}

.all__products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    gap: 1rem;
    margin: 1rem;
}